<template>
  <div class="scroll-to-top">
    <button @click="scrollToTop"></button>
  </div>
</template>

<script>
export default {
  name: 'ScrollToTop',
  methods: {
    scrollToTop() {
      window.scrollTo({
        top: 0,
        behavior: 'smooth',
      });
    },
  },
};
</script>

