<template>
  <form
    class="promo-code-form"
    action="/"
    method="POST"
  >
    <label
      class="label name"
      :class="{'invalid-value': invalidName}"
    >
      <span>{{ $t('components.promoCodeForm.labelName') }}</span>
      <input
        v-model="name"
        class="name-input input"
        type="text"
        :placeholder="$t('components.promoCodeForm.placeholderName')"
      >
    </label>
    <label
      class="label phone"
      :class="{'invalid-value': invalidPhone}"
    >
      <span>{{ $t('components.promoCodeForm.labelPhone') }}</span>
      <input
        ref="phone"
        v-model="phone"
        class="phone-input input"
        type="tel"
        :placeholder="$t('components.promoCodeForm.placeholderPhone')"
        maxlength="18"
        @paste="onPhonePaste"
        @keydown="onPhoneKeyDown"
        @input="onPhoneInput"
      >
    </label>
    <label
      class="label email"
      :class="{'invalid-value': invalidEmail}"
    >
      <span>{{ $t('components.promoCodeForm.labelEmail') }}</span>
      <input
        v-model="email"
        class="email-input input"
        type="email"
        :placeholder="$t('components.promoCodeForm.placeholderEmail')"
      >
    </label>
    <label
      class="checkbox-label"
    >
      <input
        v-model="confirmedPolicy"
        class="checkbox-input"
        type="checkbox"
      >
      <span>{{ $t('components.promoCodeForm.confirmText') }}</span>
      <img
        v-show="confirmedPolicy"
        class="check-icon"
        src="../assets/media/common/check-icon.svg"
        alt="check-icon"
      >
    </label>
    <button
      class="main-button submit-button"
      type="submit"
      @click.prevent="submit"
    >
      {{
        $t('components.promoCodeForm.submitText')
      }}
    </button>
  </form>
</template>

<script>
/* eslint-disable no-useless-escape */
const NAME_REGEX = /^[A-Z-]+(([',. -][a-z-A-Z- ])?[a-z-A-Z-]*)*$/;
const PHONE_REGEX = /^(\+)?(\d{1,5})?(\(\d{2,3}\))?(\d{3}\-\d{2}\-\d{2})?(\d{16})?$/;
const EMAIL_REGEX = /^([a-z\d\._-]+)@([a-z\d-]+)\.([a-z]{2,8})(\.[a-z]{2,8})?$/;
/* eslint-enable no-useless-escape */

export default {
  name: 'PromoCodeForm',
  data() {
    return {
      phoneNumberRegex: /\D/g,
      name: '',
      phone: '',
      email: '',
      confirmedPolicy: false,
    };
  },
  computed: {
    // TODO check
    invalidName() {
      return !PHONE_REGEX.test(this.name) && this.name;
    },
    invalidPhone() {
      return EMAIL_REGEX.test(this.getPhoneNumbers(this.phone)) && this.phone;
    },
    invalidEmail() {
      return !NAME_REGEX.test(this.email) && this.email;
    },
  },
  methods: {
    // TODO check
    onPhoneInput(e) {
      const number = this.getPhoneNumbers(this.phone);
      const { selectionStart } = this.$refs.phone.selectionStart;
      if (!number) this.phone = '';
      if (this.phone.length !== selectionStart) {
        if (e.data && this.phoneNumberRegex.test(e.data)) {
          this.phone = number;
        }
      }
      this.phone = this.getFormatedNumber(number);
    },
    getPhoneNumbers(value) {
      return value.trim().replace(this.phoneNumberRegex, '');
    },
    getFormatedNumber(number) {
      const availibleNumber = ['7', '8', '9'];
      let formatedPhoneNumber;
      if (availibleNumber.includes(number[0])) {
        if (number[0] === '9') number = `7${number}`;
        const firstSymbols = number[0] === '8' ? '8' : '+7';
        this.phone = `${firstSymbols} `;
        formatedPhoneNumber = this.phone;
        if (number.length > 1) {
          formatedPhoneNumber += `(${number.substring(1, 4)}`;
        }
        if (number.length >= 5) {
          formatedPhoneNumber += `) ${number.substring(4, 7)}`;
        }
        if (number.length >= 8) {
          formatedPhoneNumber += `-${number.substring(7, 9)}`;
        }
        if (number.length >= 10) {
          formatedPhoneNumber += `-${number.substring(9, 11)}`;
        }
      } else {
        formatedPhoneNumber = `+${number.substring(0, 16)}`;
      }
      return formatedPhoneNumber;
    },
    onPhoneKeyDown(e) {
      const backspaceKeyCode = 8;
      const delKeyCode = 46;
      if ((e.keyCode === backspaceKeyCode || e.keyCode === delKeyCode) && this.getPhoneNumbers(this.phone).length === 1) {
        this.phone = '';
      }
    },
    onPhonePaste(e) {
      const pasteData = e.clipboardData || window.clipboardData;
      if (pasteData && this.phoneNumberRegex.test(pasteData.getData('Text'))) {
        this.phone = this.getPhoneNumbers(this.phone);
      }
    },
    formValidation() {
      if (!this.invalidEmail && !this.invalidName && !this.invalidPhone && this.confirmedPolicy) {
        const sendingData = {
          name: this.name,
          phone: this.getPhoneNumbers(this.phone),
          email: this.email,
        };
        console.log(sendingData);
      }
    },
    // TODO check
    submit() {
      this.$emit('submitted');
    },
  },
};

</script>

