const RATIO_COEFICIENT = 1.7777777777777777;

export default {
  data() {
    return {
      pageWidth: null,
      pageHeight: null,
      scaleValue: 1,
    };
  },
  created() {
    this.setPageSize();
  },
  mounted() {
    window.addEventListener('resize', () => {
      this.setPageSize();
    });
  },
  methods: {
    setPageSize() {
      if (this.isBaseWidth()) {
        this.pageWidth = this.getWidth();
        this.pageHeight = this.pageWidth / RATIO_COEFICIENT;
      } else {
        this.pageHeight = this.getHeight();
        this.pageWidth = this.pageHeight * RATIO_COEFICIENT;
      }
      this.scaleValue = window.innerWidth / 1920;
    },
    isBaseWidth() {
      return window.innerWidth / window.innerHeight >= RATIO_COEFICIENT;
    },
    getWidth() {
      return window.innerHeight * RATIO_COEFICIENT;
    },
    getHeight() {
      return window.innerWidth / RATIO_COEFICIENT;
    },
  },
};
