<template>
  <div class="timer-wrapper">
    <span class="title">{{ $t('components.gameHeader.timerTitle') }}</span>
    <span class="text">{{ displayedCurrentTime }}</span>
  </div>
</template>

<script>
import Moment from 'moment';

export default {
  name: 'Timer',
  props: {
    time: {
      type: Number,
      default: 10,
    },
  },
  data() {
    return {
      timerID: null,
      currentTime: null,
      currentDayTime: null,
      displayedCurrentTime: null,
    };
  },
  watch: {
    time: {
      handler() {
        this.initTimer();
      },
      immediate: true,
    },
  },
  methods: {
    initTimer() {
      this.currentTime = Moment(this.time, 'ss');
      this.currentDayTime = Moment('00:00', 'mm:ss');
      this.$emit('percent-time-remaining', this.getPercentTimeRemaining());
      this.setDisplayedCurrentTime();
    },
    setDisplayedCurrentTime() {
      this.displayedCurrentTime = this.currentTime.format('mm:ss');
    },
    startTimer() {
      if (this.timerID) return;
      this.timerID = setInterval(() => {
        this.currentTime.subtract(1, 'seconds');
        this.setDisplayedCurrentTime();
        this.$emit('percent-time-remaining', this.getPercentTimeRemaining());
        if (this.isTimerZero()) this.stopTimer();
      }, 1000);
    },
    stopTimer() {
      clearInterval(this.timerID);
      this.timerID = null;
    },
    isTimerZero() {
      return this.currentTime.minute() === 0 && this.currentTime.seconds() === 0;
    },
    getPercentTimeRemaining() {
      const time1 = this.currentTime.valueOf() - this.currentDayTime.valueOf();
      const time2 = Moment(this.time, 'ss').valueOf() - this.currentDayTime.valueOf();
      return 100 - (time1 / time2) * 100;
    },
  },
};
</script>

