<template>
  <div
    class="popup popup-level-losing"
    :class="{opened}"
    @transitionend="hideContent"
  >
    <div
      v-show="shownContent"
      class="popup-content"
    >
      <div class="border-block">
        <div
          v-show="isSocialsAvailable"
          class="socials-wrapper"
        >
          <h3 class="title">
            {{ $t('components.PopupSocialsSharing.socialsTitle') }}
          </h3>
          <div class="socials">
            <div
              v-show="isVkSocialAvailable"
              class="social vk"
            >
              <div
                ref="vkImg"
                class="social-image"
              >
                <div class="social-icon vk" />
              </div>
              <div
                ref="vkLink"
                class="socials-label"
              />
            </div>
          </div>
          <p class="share-text1">
            {{ $t('components.PopupSocialsSharing.shareText1') }}
            <span class="score-value"> {{ scoreValue }} </span>
            <span class="score-value-description"> {{ scoreValueDescription }} </span>
          </p>
          <p class="share-text2">
            <span class="level-value-description"> {{ $t('components.PopupSocialsSharing.shareText2') }}</span>
            <span class="level-value">"{{ levelDescription }}"</span>
          </p>
          <button
            class="main-button close-popup-button"
            @click="closePopup"
          >
            {{ $t('components.PopupSocialsSharing.cancelButtonTitle') }}
          </button>
        </div>

        <div
          v-show="!isSocialsAvailable && !isPreloaderActive"
          class="no-available-socials"
        >
          <h3 class="title">
            {{ $t('components.PopupSocialsSharing.socialsNotAvailable') }}
          </h3>
          <button
            class="main-button close-popup-button"
            @click="closePopup"
          >
            {{ $t('components.PopupSocialsSharing.closeButtonTitle') }}
          </button>
        </div>

        <div
          v-show="isPreloaderActive"
          class="socials-preloader"
        >
          <h3 class="title">
            {{ $t('components.PopupSocialsSharing.preloaderTitle') }}
          </h3>
          <div class="preloader">
            <loader
              :object="loaderOptions.object"
              :color1="loaderOptions.color1"
              :color2="loaderOptions.color2"
              :size="loaderOptions.size"
              :speed="loaderOptions.speed"
              :bg="loaderOptions.bg"
              :objectbg="loaderOptions.objectBg"
              :opacity="loaderOptions.opacity"
              :disable-scrolling="!isPreloaderActive"
              :name="loaderOptions.name"
            />
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { numberEnd } from '@/utils/index';

export default {
  name: 'PopupSocialsSharing',
  props: {
    opened: {
      type: Boolean,
      default: false,
    },
    scoreValue: {
      type: Number,
      default: 50,
    },
    levelValue: {
      type: Number,
      default: 3,
    },
    socialsOptions: {
      type: Object,
      default: () => ({
        share_url: '',
        images: {},
        socials: {
          vk: {},
        },
      }),
    },
  },
  emits: ['update:opened'],
  data() {
    return {
      shownContent: false,
      isVkSocialAvailable: false,
      isSocialsAvailable: false,
      isPreloaderActive: true,
      loaderOptions: {
        object: '#f5ba01',
        color1: '#ffffff',
        color2: '#17fd3d',
        size: 5,
        speed: 2,
        bg: '#343a40',
        objectBg: '#dee8ff',
        opacity: 0,
        name: 'spinning',
      },
    };
  },
  computed: {
    scoreValueDescription() {
      return numberEnd(this.scoreValue, [
        this.$t('components.PopupSocialsSharing.scoreTextType1'),
        this.$t('components.PopupSocialsSharing.scoreTextType2'),
        this.$t('components.PopupSocialsSharing.scoreTextType3'),
      ]);
    },
    levelDescription() {
      return this.$t(`components.PopupSocialsSharing.level${this.levelValue}`);
    },
    // vkShareLink() {
    //   return `https://vk.com/share.php?url=http://oleg-dev.github.io/result${this.levelValue + 1}`;
    // },
    socialsShareText() {
      return `${this.$t('components.PopupSocialsSharing.shareText3') + this.scoreValue} ${this.scoreValueDescription}`;
    },
  },
  watch: {
    opened: {
      immediate: true,
      handler(value) {
        if (value) this.showContent();
        const vkOptions = this.socialsOptions.socials.vk;
        if (value) this.getVkScriptAvailability(vkOptions.script_URL, vkOptions.check_timeout);
      },
    },
  },
  methods: {
    closePopup() {
      // this.$emit('update:opened', false);
      this.$emit('close');
    },
    showContent() {
      this.shownContent = true;
    },
    hideContent() {
      if (!this.opened) this.shownContent = false;
    },
    getVkScriptAvailability(url, timeout) {
      const scriptEl = document.createElement('script');
      scriptEl.setAttribute('src', url);
      scriptEl.addEventListener('load', this.showVkSocial);
      document.head.appendChild(scriptEl);
      setTimeout(() => {
        if (!this.isVkSocialAvailable) {
          scriptEl.removeEventListener('load', this.showVkSocial);
          scriptEl.parentNode.removeChild(scriptEl);
        }
        this.isPreloaderActive = false;
      }, timeout);
    },
    showVkSocial() {
      this.isVkSocialAvailable = true;
      this.initVkButton();
      this.isSocialsAvailable = true;
      this.isPreloaderActive = false;
    },
    initVkButton() {
      this.$refs.vkLink.innerHTML = window.VK.Share.button({
        url: this.socialsOptions.share_url,
        title: this.socialsShareText,
        image: this.socialsOptions.images[`level${this.levelValue}`],
      }, { type: 'custom', text: this.$t('components.PopupSocialsSharing.vk') });
      const vkLinks = [...this.$refs.vkLink.childNodes].filter((el) => el.tagName.toLowerCase() === 'a');
      vkLinks[0].append(this.$refs.vkImg);
    },
  },
};
</script>

